.survey-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 110vh;
  background: linear-gradient(to right, #142551, #385089);
      
}

.survey-box {
  padding: 70px;
  width: 450px;
  text-align: center;
  background:#ffffff;
  border-radius: 10px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
}

.welcome-text {
  font-weight: bold;
  color: #142551;
}

.sub-text {
  color: #646262;
  margin-bottom: 20px;
  
}

.form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.select-field {
  text-align: left;
}

.confidentiality-box { 
  margin-top: 10px;
  text-align: left;
}

.confidentiality-text {
  color: #444;
  font-size: 14px;
}

.error-text {
  color: red;
  font-size: 14px;
  margin-top: 10px;
}

.submit-btn {
  background-color: #142551;
  color: white;
  padding: 10px;
  border-radius: 5px;
  font-size: 16px;
  transition: 0.3s;
}

.submit-btn:hover {
  background-color: #0e1b3c;
}